// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';

// import required modules
import { Autoplay, Pagination, Navigation } from 'swiper/modules';

const MainPage = () => {
    return (
        <div className="main">
            <Swiper
                /* spaceBetween={30} */
                centeredSlides={true}
                autoplay={{
                    delay: 2500,
                    disableOnInteraction: false,
                }}
                pagination={{
                    clickable: true,
                }}
                navigation={true}
                modules={[Autoplay, Pagination, Navigation]}
                className="mySwiper"
            >
                <SwiperSlide><img src="img/image_1" alt="image1" /></SwiperSlide>
                <SwiperSlide><img src="img/image_2" alt="image2" /></SwiperSlide>
                <SwiperSlide><img src="img/image_3" alt="image3" /></SwiperSlide>

            </Swiper>
        </div>
    )
}

export default MainPage;